import React, { useMemo } from 'react';
import { useParams } from 'react-router';

import { isMatchedById } from '@mssgme/shared';
import { NotificationsBag, ThemeContext } from '@mssgme/ui';

import { useAutoTheme, useWebsiteData } from '../hooks';
import { PastDueModal } from '../UI';
import { Page } from '../Page';

export const PageContainer = () => {
    const { pageSlug } = useParams();
    const website = useWebsiteData();
    const page = useMemo(() => {
        if (pageSlug) {
            const found = website.pages.find(({ slug }) => slug === pageSlug);

            if (found) {
                return found;
            }
        }

        return website.pages.find(isMatchedById(website.homePageId));
    }, [pageSlug, website]);

    const { theme } = useAutoTheme(page.theme);

    return (
        <ThemeContext.Provider value={theme}>
            <NotificationsBag/>

            <Page
                website={website}
                page={page}
                footer={website.unpublishedAt && <PastDueModal/>}
            />
        </ThemeContext.Provider>
    );
};
